/* React */
import React from "react";

interface IconProps {
  children?: JSX.Element | JSX.Element[];
  extraStyle?: React.CSSProperties;
  onClick?: React.MouseEventHandler<HTMLDivElement> | undefined;
}

const baseClass = "icon";
const templateClasses =
  "flex items-center justify-center rounded-full bg-blue-500 h-10  w-10 overflow-hidden cursor-pointer bg-purple-400";

const Icon = ({ children, extraStyle, onClick }: IconProps) => {
  const className = `${baseClass} ${templateClasses}`;
  return (
    <div style={extraStyle} className={className}>
      {children}
    </div>
  );
};

export default Icon;
